import React, { useState } from "react"
import API from "../../utils/Api"

import TextField from "@material-ui/core/TextField"
import Button from "@material-ui/core/Button"
import { Row, Col, Form, Spinner } from "reactstrap"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faArrowAltCircleLeft, faInfoCircle, faPaperPlane } from "@fortawesome/free-solid-svg-icons"
import Error from "../UI/Error/Error"

const Forgot = (props) => {

    const [isProcessing, setProcessing] = useState(false)
    const [isError, setIsError] = useState(false)
    const [errorMessage, setErrorMessage] = useState(
        "Sorry, something wen't wrong"
    )

    const [isSubmitted, setIsSubmitted] = useState(false)
    const [email, setEmail] = useState("")

    const forgotPasswordHandler = (e) => {
        e.preventDefault()

        if (email !== "") {
            setProcessing(true)
            setIsError(false)
            setIsSubmitted(false)
            API.post("forgot", {
                email
            })
                .then((result) => {
                    setProcessing(false)
                    if (result.status === 200) {
                        if (result.data.status === false) {
                            setErrorMessage(result.data.error)
                            setIsError(true)
                        } else {
                            setIsSubmitted(true)
                            setEmail('')
                        }
                    } else {
                        setIsError(true)
                    }
                })
                .catch((e) => {
                    setProcessing(false)
                    setIsError(true)
                })
        } else {
            setErrorMessage("Please enter your email")
            setIsError(true)
        }
    }

    const backToLogin = (e) => {
        e.preventDefault()
        props.toggle()
    }

    const handleEmailChange = (email) => {
        setIsSubmitted(false)
        setEmail(email)
    }

    return (
        <Col xs={12} style={{ marginTop: "4%" }}>
            <Form onSubmit={(e) => forgotPasswordHandler(e)} autoComplete="off">
                <Col xs={12} className="pl-4 pr-4 mb-2 text-center">
                    {isError && <Error>{errorMessage}</Error>}
                </Col>
                <Col xs={12} className="ml-2 mb-1">
                    {!isSubmitted && (<span>Forgot your password?</span>)}<br />
                </Col>
                {isSubmitted && (
                    <Col xs={12} className="text-center mt-4 mb-5 pb-2">
                        <span className="text-success"><FontAwesomeIcon icon={faInfoCircle} /> Check your email inbox for the link we sent to you.</span>
                    </Col>
                )}
                <Col xs={12} className="pl-4 pr-4">
                    {!isSubmitted && (
                        <TextField
                            value={email}
                            onChange={(e) => handleEmailChange(e.target.value)}
                            type="email"
                            name="email"
                            placeholder="Email"
                            label="Email"
                            fullWidth
                            required
                        />
                    )}
                </Col>
                <Col xs={12} className="pl-4 pr-4 pt-1 mt-4 mb-4">
                    {isProcessing ? (
                        <div
                            className="text-center mt-1"
                            style={{ minHeight: "100px" }}
                        >
                            <Spinner
                                className="mt-4"
                                style={{ width: "2rem", height: "2rem" }}
                                color="primary"
                            />
                            <br />
                            <span className="mt-1">Processing...</span>
                        </div>
                    ) : (
                        <div>
                            {!isSubmitted && (
                                <Button
                                    type="submit"
                                    variant="contained"
                                    color="primary"
                                    className="pt-1"
                                    disabled={isProcessing}
                                    fullWidth
                                >
                                    <span>Submit</span>
                                    <FontAwesomeIcon
                                        className="pl-1"
                                        icon={faPaperPlane}
                                    />
                                </Button>
                            )}

                            <Row className="mt-4 pt-2">
                                <Col md={12} className="text-center">
                                    <a
                                        href="#"
                                        variant="link"
                                        onClick={(e) => backToLogin(e)}
                                        className="pt-1 btn text-primary"
                                        disabled={isProcessing}
                                    >
                                        <FontAwesomeIcon
                                            className="pr-1"
                                            icon={faArrowAltCircleLeft}
                                        />
                                        <span>Back to Login</span>
                                    </a>
                                </Col>
                            </Row>
                        </div>
                    )}
                </Col>
            </Form>
        </Col>
    )
}

export default Forgot
