import React, { useState } from "react"
import { useAuth } from "../../context/auth"
import API from "../../utils/Api"

import { makeStyles } from "@material-ui/core/styles"
import TextField from "@material-ui/core/TextField"
import InputAdornment from "@material-ui/core/InputAdornment"
import IconButton from "@material-ui/core/IconButton"
import Visibility from "@material-ui/icons/Visibility"
import VisibilityOff from "@material-ui/icons/VisibilityOff"
import FacebookIcon from "@material-ui/icons/Facebook"
import TwitterIcon from "@material-ui/icons/Twitter"
import InstagramIcon from "@material-ui/icons/Instagram"
import YouTubeIcon from "@material-ui/icons/YouTube"
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos"
import Button from "@material-ui/core/Button"

import { Container, Row, Col, Card, Form, Spinner } from "reactstrap"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faQuestion, faSignInAlt, faUserPlus } from "@fortawesome/free-solid-svg-icons"
import "../Login/login.css"
import Logo from "../../assets/logo.jpg"
import MARINA from "../../assets/marina.png"
import DTO from "../../assets/dto.png"
import Error from "../UI/Error/Error"
import Info from "../UI/Info/Info"
import { Avatar } from "@material-ui/core"
import Forgot from "./Forgot"

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    "& > *": {
      margin: theme.spacing(1),
    },
  },
  small: {
    width: theme.spacing(2.3),
    height: theme.spacing(2.3),
    float: "left",
    marginLeft: "7px",
    marginTop: "3px",
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
}))

const Login = (props) => {

  const classes = useStyles()

  const { authTokens, setAuthTokens } = useAuth()

  const [isProcessing, setProcessing] = useState(false)
  const [isError, setIsError] = useState(false)
  const [errorMessage, setErrorMessage] = useState(
    "Sorry, Server Error"
  )
  const [isInfo, setIsInfo] = useState(false)
  const [infoMessage, setInfoMessage] = useState(
    "Sorry, Something Wen't Wrong"
  )

  const [isShowPass, setIsShowPass] = useState(false)
  const toggleShowPassword = () => setIsShowPass(!isShowPass)

  const [isShowForgotPassword, setIsShowForgotPassword] = useState(false)
  const toggleIsForgotPassword = () => setIsShowForgotPassword(!isShowForgotPassword)


  const isAuthenticatedHandler = () => {
    const token = authTokens

    if (token === null) {
      return false
    } else {
      if (token.expires_in < Date.now()) {
        return true
      }
    }
    return false
  }

  if (isAuthenticatedHandler()) {
    props.history.push('/')
  }

  const loginHandler = (e) => {
    e.preventDefault()
    const username = document.querySelector("input[name=username]").value
    const password = document.querySelector("input[name=password]").value

    if (username !== "" && password !== "") {
      setProcessing(true)
      setIsError(false)
      setIsInfo(false)
      API.post("auth/carrier/login", {
        username,
        password,
      })
        .then((result) => {
          setProcessing(false)
          if (result.status === 200) {
            if (result.data.status === false) {
              setErrorMessage(result.data.error)
              setIsError(true)
            } else {
              setAuthTokens(result.data)
            }
          } else if (result.status === 254) {
            setInfoMessage("Pending account approval, contact the Admin")
            setIsInfo(true)
          } else {
            setIsError(true)
          }
        })
        .catch((e) => {
          setProcessing(false)
          setIsError(true)
        })
    } else {
      setErrorMessage("Please enter a username and password")
      setIsError(true)
    }
  }

  const handleForgotPassword = (e) => {
    e.preventDefault()
    toggleIsForgotPassword()
  }

  return (
    <Container style={{ width: "959px", marginTop: "5%" }}>
      <a
        href="https://passengertracer.net/"
        rel="noopener noreferrer"
        className="btn m-0 p-0 mb-1 text-white"
      >
        <ArrowBackIosIcon style={{ fontSize: "0.9em" }} /> Go Back to Portal
      </a>
      <Card
        className="bg-primary p-0 m-0"
        style={{ borderRadius: "0px", border: "none", opacity: "0.9" }}
      >
        <Row className="p-0 m-0">
          <Col xs={6} className="text-center text-white p-2">
            <img
              className="mt-5"
              src={MARINA}
              width="90"
              height="110"
              alt="Marina Logo"
            />
            <a
              href="https://marina.gov.ph/"
              rel="noopener noreferrer"
              target="_blanks"
              className="text-white ml-1 mr-1"
              style={{ textDecoration: "none" }}
            >
              <h3 className="mt-4">Maritime Industry Authority</h3>
            </a>
            <div className="text-center pl-5 pr-5 ml-0">
              Follow us on: <br />
              <a
                href="https://www.facebook.com/DOTrMARINAPH"
                rel="noopener noreferrer"
                target="_blanks"
                className="text-white mr-1"
                style={{ textDecoration: "none" }}
              >
                <FacebookIcon className={classes.large} />
              </a>
              <a
                href="https://twitter.com/DOTrMARINAPH"
                rel="noopener noreferrer"
                target="_blanks"
                className="text-white ml-1 mr-1"
                style={{ textDecoration: "none" }}
              >
                <TwitterIcon className={classes.large} />
              </a>
              <a
                href="https://www.instagram.com/dotrmarinaph/"
                rel="noopener noreferrer"
                target="_blanks"
                className="text-white ml-1 mr-1"
                style={{ textDecoration: "none" }}
              >
                <InstagramIcon className={classes.large} />
              </a>
              <a
                href="https://www.youtube.com/channel/UCK9SPcPFhiWLBUXAzlQqsxg"
                rel="noopener noreferrer"
                target="_blanks"
                className="text-white ml-1 mr-1"
                style={{ textDecoration: "none" }}
              >
                <YouTubeIcon className={classes.large} />
              </a>
            </div>
            <div
              style={{
                fontSize: "0.9rem",
                marginTop: "25%",
                marginLeft: "10%",
              }}
            >
              <span className="float-left">Powered By: </span>
              <a
                target="_blank"
                href="https://www.facebook.com/ustpdigitaltransformation"
                rel="noopener noreferrer"
                className="text-white"
                style={{ textDecoration: "none" }}
              >
                <Avatar src={DTO} className={classes.small} />
                <span className="float-left ml-1">
                  USTP - Digital Transformation Office
                </span>
              </a>
            </div>
          </Col>
          <Col xs={6} className="p-0 m-0">
            <Card className="p-0 m-0" style={{ borderRadius: "0px" }}>
              <Col xs={12} className="mt-4 text-center">
                <img
                  className="mt-2"
                  src={Logo}
                  style={{ width: "8rem", height: "10rem" }}
                  alt="Passenger eTracer Logo"
                />
                <h5>Transport Module</h5>
              </Col>
              {!isShowForgotPassword && (
                <Col xs={12}>
                  <Form onSubmit={(e) => loginHandler(e)} autoComplete="off">
                    <Col xs={12} className="pl-4 pr-4 mb-2 text-center">
                      {isError && <Error>{errorMessage}</Error>}
                      {isInfo && <Info>{infoMessage}</Info>}
                    </Col>
                    <Col xs={12} className="pl-4 pr-4 mb-2">
                      <TextField
                        name="username"
                        placeholder="Username"
                        label="Username"
                        fullWidth
                        required
                      />
                    </Col>
                    <Col xs={12} className="pl-4 pr-4 mb-2">
                      <TextField
                        type={isShowPass ? "text" : "password"}
                        name="password"
                        placeholder="Password"
                        label="Password"
                        fullWidth
                        required
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton onClick={toggleShowPassword}>
                                {isShowPass ? <Visibility /> : <VisibilityOff />}
                              </IconButton>
                            </InputAdornment>
                          ),
                        }}
                      />
                    </Col>
                    <Col xs={12} className="pl-4 pr-4 mt-3 mb-4">
                      {isProcessing ? (
                        <div
                          className="text-center mt-1"
                          style={{ minHeight: "100px" }}
                        >
                          <Spinner
                            className="mt-4"
                            style={{ width: "2rem", height: "2rem" }}
                            color="primary"
                          />
                          <br />
                          <span className="mt-1">Processing...</span>
                        </div>
                      ) : (
                        <div>
                          <Button
                            type="submit"
                            variant="contained"
                            color="primary"
                            className="pt-1"
                            disabled={isProcessing}
                            fullWidth
                          >
                            <span>LOGIN</span>
                            <FontAwesomeIcon
                              className="pl-1"
                              icon={faSignInAlt}
                            />
                          </Button>
                          <Row className="mt-4 pt-1">
                            <Col md={6} className="text-left ml-0 pl-1">
                              <a
                                href="#"
                                variant="link"
                                onClick={(e) => handleForgotPassword(e)}
                                className="pt-1 btn text-primary"
                                disabled={isProcessing}
                              >
                                <span>Forgot Password</span>
                                <FontAwesomeIcon
                                  className="pl-1"
                                  icon={faQuestion}
                                />
                              </a>
                            </Col>
                            <Col md={6} className="text-right mr-0 pr-1">
                              <a
                                href="register"
                                variant="link"
                                className="pt-1 btn text-primary"
                                disabled={isProcessing}
                              >
                                <FontAwesomeIcon
                                  className="pr-1"
                                  icon={faUserPlus}
                                />
                                <span>Register Here</span>
                              </a>
                            </Col>
                          </Row>
                        </div>
                      )}
                    </Col>
                  </Form>
                </Col>
              )}
              {isShowForgotPassword && (
                <Forgot toggle={toggleIsForgotPassword} />
              )}
            </Card>
          </Col>
        </Row>
      </Card>
    </Container >
  )
}

export default Login
